.form{
    
    margin: auto;
    max-width: 40rem;
}
.inputform{
    max-width: 20rem;
    margin-top: 50px;
   
}
.input{
    font-size: 30px;
    border: 1px solid gray;
}

.btn{
    display: block;
    
    margin: auto;
    font-size: 20px;
    padding-left: 35px;
    padding-right: 35px;
    border-radius: 15px;
    padding: 20px;
    margin-top: 15px;
    background-color: blueviolet;
    border-style: none;
    color: white;
   
}
.image{
    height: 40rem;
    margin: auto;
   width: 40rem;
   display: block;
   border-radius: 10px;
   object-fit: cover;
}



.meme--text {
    position: absolute;
    width: 80%;
    text-align: center;
    left: 50%;
    transform: translateX(-50%);
    margin: 325px 0;
    padding: 0 5px;
    font-family: impact, sans-serif;
    font-size: 2em;
    text-transform: uppercase;
    color: white;
    letter-spacing: 1px;
    text-shadow:
        2px 2px 0 #000,
        -2px -2px 0 #000,
        2px -2px 0 #000,
        -2px 2px 0 #000,
        0 2px 0 #000,
        2px 0 0 #000,
        0 -2px 0 #000,
        -2px 0 0 #000,
        2px 2px 5px #000;
}

.bottom {
    bottom: 0;
}

.top {
    top: 0;
}